<template>
    <section id="welcome" class="image-background position-relative">
        <div class="img-wrapper">
            <img src="../../assets/cassino.jpg" alt="" class="img-test">
            <button :id="'hotspot-'+index" class="blob white" type="button" v-for="index in 9" :key="index"></button>
        </div>
        <DirectionArrow :componentLink="this.componentLink"/>
    </section>
</template>
<style lang="scss">

.blob {
    background: black;
    border-radius: 50%;
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 1);
    margin: 10px;
    height: 20px;
    width: 20px;
    transform: scale(1);
    animation: pulse-black 2s infinite;
}

.blob.white {
    background: rgba(255, 255, 255, 0.3);
    box-shadow: 0 0 0 0 rgba(255, 255, 255, 1);
    animation: pulse-white 2s infinite;
}

@keyframes pulse-white {
    0% {
        transform: scale(0.95);
        box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.7);
    }

    70% {
        transform: scale(1);
        box-shadow: 0 0 0 30px rgba(255, 255, 255, 0);
    }

    100% {
        transform: scale(0.95);
        box-shadow: 0 0 0 0 rgba(255, 255, 255, 0);
    }
}

.img-wrapper {
    display: inline-block;
    position: relative;
    min-width: 320px; /* MOBILE WIDTH */
    max-width: 100%; /* B */
    width: 100%; /* B */
}

.img-test {
    /*max-width: 100%; /* A */
    width: 100%; /* B */
    height: auto;
    display: block;
}

[id^="hotspot"] {
    color: #fff;
    text-align: center;
    line-height: 45px;
    position: absolute;
    outline: none;

    /* THIS WITH AND HEIGHT WAS DETERMINED BY TRY AND ERROR, AND WORKS ONLY FOR  THE RATIO OF THE CURRENT IMAGE */
    width: 6%;
    height: 11%;

    margin: -5.5% 0 0 -3%; /* THIS CENTER THE TOP AND LEFT REFERENCE POINT TO THE CENTER OF THE HOTSPOT (ex.: 6%/2 = 3% ) */

    border-radius: 50%;
    background: rebeccapurple;
    z-index: 2;
    cursor: pointer;
    border: none;

    &#hotspot-1 {
        top: 12.5%;
        left: 49.7%;
    }

    &#hotspot-2 {
        top: 21%;
        left: 64%;
    }

    &#hotspot-3 {
        top: 11%;
        left: 74%;
    }

    &#hotspot-4 {
        top: 19%;
        left: 19%;
    }

    &#hotspot-5 {
        top: 37%;
        left: 17%;
    }

    &#hotspot-6 {
        top: 56.5%;
        left: 27.5%;
    }

    &#hotspot-7 {
        top: 80%;
        left: 13%;
    }

    &#hotspot-8 {
        top: 77%;
        left: 31.5%;
    }

    &#hotspot-9 {
        top: 77%;
        left: 47%;
    }
}

#welcome .map {
    width: 100%;
    height: auto;
}

.image-background {
    background: #364b51;
}
</style>
<script>
import DirectionArrow from "@/components/Layout/DirectionArrow";
export default {
    props:[
        'componentLink'
    ],
    components: {DirectionArrow}
}
