<template>
    <div class="arrow-directions position-absolute end-50">
        <a v-smooth-scroll :href="this.componentLink">
            <div class="chevron"></div>
            <div class="chevron"></div>
            <div class="chevron"></div>
        </a>
    </div>
</template>
<script>
export default {
    props: [
        'componentLink'
    ]
}
</script>
<style lang="scss">
$base: 0.6rem;
.arrow-directions {
    bottom: 0rem;
}

.arrow-directions a {
    height: 5rem;
    width: 2rem;
    display: block;
}

.chevron {
    position: absolute;
    width: $base * 3.5;
    height: $base * 0.8;
    opacity: 0;
    transform: scale(0.3);
    animation: move-chevron 3s ease-out infinite;
}

.chevron:first-child {
    animation: move-chevron 3s ease-out 1s infinite;
}

.chevron:nth-child(2) {
    animation: move-chevron 3s ease-out 2s infinite;
}

.chevron:before,
.chevron:after {
    content: '';
    position: absolute;
    top: 0;
    height: 100%;
    width: 50%;
    background: #fff;
}

.chevron:before {
    left: 0;
    transform: skewY(30deg);
}

.chevron:after {
    right: 0;
    width: 50%;
    transform: skewY(-30deg);
}

@keyframes move-chevron {
    25% {
        opacity: 1;
    }
    33.3% {
        opacity: 1;
        transform: translateY($base * 3.8);
    }
    66.6% {
        opacity: 1;
        transform: translateY($base * 5.2);
    }
    100% {
        opacity: 0;
        transform: translateY($base * 8) scale(0.5);
    }
}
</style>